import React,{useState} from 'react';
import './App.css';
import Browser from './Components/Browser';
import PageContextProvider from './Context/PageContext'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

function App() {
  const [darkMode, setDarkMode] = useState(false);

  const themePicker = darkMode ? createMuiTheme({
    palette: {
      type: 'dark',
      primary: {
        light: '#a6d4fa',
        main: '#90caf9',
        dark: '#648dae'
      }
    },
  }) : createMuiTheme({});

  return (
    <div className="App" style={darkMode ? {backgroundColor:'#121212'}:null}>
      <PageContextProvider>  
      <ThemeProvider theme={themePicker}>
        <Browser darkMode={darkMode} setDarkMode={setDarkMode} />
      </ThemeProvider>
      </PageContextProvider>
    </div>
  );
}

export default App;
