import React, {useState, useEffect, createRef} from 'react';
import PropTypes from 'prop-types';
import scss from './Browser.module.scss';
import clsx from 'clsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons'
import { faArrowRight } from '@fortawesome/free-solid-svg-icons'
import { faRedo } from '@fortawesome/free-solid-svg-icons'
import { faCode } from '@fortawesome/free-solid-svg-icons'
import { faMinus } from '@fortawesome/free-solid-svg-icons'
import { faSquare } from '@fortawesome/free-regular-svg-icons'
import { faStar } from '@fortawesome/free-regular-svg-icons'
import { faEllipsisV } from '@fortawesome/free-solid-svg-icons'
import MainMenu from '../MainMenu'
import Typewriter from '../Typewriter'
import Tree from '../Demos/Tree'
import Table from '../Demos/Table/Traffic'
import AG from '../Demos/AGgrid'
import Noko from '../Demos/Noko'
import Experience from '../Experience'
import SecretPage from '../Demos/SecretPage'

import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import {PageContext} from '../../Context/PageContext';
import { Typography } from '@material-ui/core';
import Switch from '@material-ui/core/Switch';
import Divider from '@material-ui/core/Divider';

const dmBase = '#121212';


function NameBox(props) {

  const componentRef = createRef();

  const handleClickOutside = (event) => {
    if (event.target.innerText !== ('X') && componentRef.current && !componentRef.current.contains(event.target) ) {
      props.handleClose();
    }
  }

  useEffect(()=>{
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    }
  },[componentRef])

  const innerContent = (
    <>
      <div ref={componentRef} className={clsx(scss.nameBoxCircle)}>X</div>
      <span className={clsx(scss.nameBoxName)}>Xan</span>
      <span className={clsx(scss.nameBoxTitle)}>Front-End Developer</span>
    </>
  )

  return (
    <div style={props.darkMode ? {backgroundColor:'#121212'}:null} ref={componentRef} className={clsx(scss.nameBox, [props.open && scss.nameBoxOpen ])}>
      {props.darkMode && <div className={scss.overlay}>
        {innerContent}
      </div>}
      {!props.darkMode && innerContent}
    </div>
  )
}
NameBox.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
};

function SettingsBox(props) {

  const componentRef = createRef();

  const handleClickOutside = (event) => {
    if (event.target.id !== ('settingsMenu') && componentRef.current && !componentRef.current.contains(event.target) ) {
      props.handleClose();
    }
  }

  useEffect(()=>{
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    }
  },[componentRef])

  const innerContent = (
    <>
    <div className={scss.settingRow}>
      <Typography style={props.darkMode ? {color:'#fff'}:null} className={scss.settingRowText}>Dark Mode</Typography>
      <Switch
        checked={props.darkMode}
        onChange={e=>props.setDarkMode(e.target.checked)}
        color="primary"
        name="darkmodeswitch"
        inputProps={{ 'aria-label': 'dark mode checkbox' }}
      />
    </div>
    <Divider className={scss.divider}/>
    </>
  )

  return (
    <div style={props.darkMode ? {backgroundColor:'#121212'}:null} ref={componentRef} className={clsx(scss.nameBox, [props.open && scss.nameBoxOpen ],scss.settingsBox)}>
      {props.darkMode && <div style={{width:'100%',height:'100%',backgroundColor:'#ffffff12'}}>
        {innerContent}
      </div>}
      {!props.darkMode && innerContent}
    </div>
  )
}
SettingsBox.propTypes = {
  darkMode: PropTypes.bool.isRequired,
  setDarkMode: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
};

function TabRow(props) {

  const {
    curPage,
    pageNames,
    handleSetCurPage,
    handleTabRemove,
  } = React.useContext(PageContext) 

  const handleCloseTab = (e,page) => {
    e.preventDefault()
    e.stopPropagation()
    handleTabRemove(page)
  }
  const handleTabClick = (page) => {
    if(curPage.page === page) return
    handleSetCurPage(page)
  }

  return (
    <div className={clsx(scss.top, scss.tabRow,[props.darkMode && scss.darkMode])}>
      { curPage.tabList.includes(pageNames.MAIN) &&
      <div onClick={()=>handleTabClick(pageNames.MAIN)} className={clsx(scss.tab,[props.darkMode && scss.darkMode], [curPage.page === pageNames.MAIN ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText, scss.newTab)}>New Tab</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.MAIN)} className={clsx(scss.tabContent, scss.closeTabDiv, [curPage.tabList.length === 1 && scss.mainTabClose])}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.AG) &&
      <div onClick={()=>handleTabClick(pageNames.AG)} className={clsx(scss.tab,[props.darkMode && scss.darkMode], [curPage.page === pageNames.AG ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>AG</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.AG)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.NOKO) &&
      <div onClick={()=>handleTabClick(pageNames.NOKO)} className={clsx(scss.tab,[props.darkMode && scss.darkMode], [curPage.page === pageNames.NOKO ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>NOKO</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.NOKO)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.TREE) &&
      <div onClick={()=>handleTabClick(pageNames.TREE)} className={clsx(scss.tab,[props.darkMode && scss.darkMode], [curPage.page === pageNames.TREE ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>TREE MENU</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.TREE)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.TABLE) &&
      <div onClick={()=>handleTabClick(pageNames.TABLE)} className={clsx(scss.tab,[props.darkMode && scss.darkMode], [curPage.page === pageNames.TABLE ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>CUSTOM TABLE</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.TABLE)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      { curPage.tabList.includes(pageNames.EXP) &&
      <div onClick={()=>handleTabClick(pageNames.EXP)} className={clsx(scss.tab,[props.darkMode && scss.darkMode], [curPage.page === pageNames.EXP ? scss.tab_active : scss.tabInactive])}>
        <span  className={clsx(scss.tabContent, scss.tabText)}>WORK</span>
        <div onClick={(e)=>handleCloseTab(e,pageNames.EXP)} className={clsx(scss.tabContent, scss.closeTabDiv)}>
          <FontAwesomeIcon className={clsx(scss.closeTab)} icon={faTimes}  />
        </div>
      </div>
      }
      <FontAwesomeIcon onClick={curPage.tabList.length <= 0 ? ()=>handleSetCurPage('main') : null} className={clsx(scss.tabPlus,[props.darkMode && scss.darkMode])} icon={faPlus}  />
      <div className={scss.flexGrow}></div>
      <FontAwesomeIcon className={clsx(scss.tabRowIconsCorner,[props.darkMode && scss.darkMode])} icon={faMinus}  />
      <FontAwesomeIcon className={clsx(scss.tabRowIconsCorner,[props.darkMode && scss.darkMode])} icon={faSquare}  />
      <FontAwesomeIcon onClick={()=>window.close()} className={clsx(scss.tabRowIconsCorner,[props.darkMode && scss.darkMode])} icon={faTimes}  />
    </div>
  )
}

function AddressRow({darkMode, setDarkMode}) {
  const [open, setOpen] = useState(false);
  const [openSettings, setOpenSettings] = useState(false);

  const {
    curPage,
  } = React.useContext(PageContext) 

  const handleClose = () => {
    setOpen(false);
  }
  const handleCloseSettings = () => {
    setOpenSettings(false);
  }
  return (
    <div className={clsx(scss.top, scss.addressRow, [darkMode && scss.darkMode])}>
      <FontAwesomeIcon className={clsx(scss.addressIcons, [darkMode && scss.darkMode])} icon={faArrowLeft}  />
      <FontAwesomeIcon className={clsx(scss.addressIcons, [darkMode && scss.darkMode])} icon={faArrowRight}  />
      <FontAwesomeIcon className={clsx(scss.addressIcons, [darkMode && scss.darkMode])} icon={faRedo}  />
      <div className={clsx(scss.urlField, [darkMode && scss.darkMode])}>
        <FontAwesomeIcon className={clsx(scss.urlIcon, [darkMode && scss.darkMode])} icon={faCode}  />
        <Typewriter darkMode={darkMode} url={curPage.fakeUrl} />
        {/* <span>Xander's Front-End Development</span> */}
        <FontAwesomeIcon className={clsx(scss.urlIcon, [darkMode && scss.darkMode])} icon={faStar}  />
      </div>
      <div className={clsx(scss.addressRowRight, [darkMode && scss.darkMode])}>
        <div onClick={()=>{if(!open){setOpen(true)}else{setOpen(false)}}} className={clsx(scss.userCircle, [darkMode && scss.darkMode])}>X</div>
        <NameBox darkMode={darkMode} open={open} handleClose={handleClose} />
        <FontAwesomeIcon id='settingsMenu' onClick={()=>{if(!openSettings){setOpenSettings(true)}else{setOpenSettings(false)}}} className={clsx(scss.urlIcon, [darkMode && scss.darkMode])} icon={faEllipsisV}  />
        <SettingsBox darkMode={darkMode} setDarkMode={setDarkMode} open={openSettings} handleClose={handleCloseSettings} />
      </div>
    </div>
  )
}
function Content() {
  const {
    curPage,
    pageNames,
  } = React.useContext(PageContext)

  let CurrentPage
  switch (curPage.page) {
    case pageNames.MAIN:
      CurrentPage = <MainMenu />
      break;
    case pageNames.NOKO:
      CurrentPage = <Noko />
      break;
    case pageNames.TREE:
      CurrentPage = <Tree />
      break;
    case pageNames.TABLE:
      CurrentPage = <Table />
      break;
    case pageNames.AG:
      CurrentPage = <AG />
      break;
    case pageNames.EXP:
      CurrentPage = <Experience />
      break;
    case pageNames.SHH:
      CurrentPage = <SecretPage />
      break;
  
    default:
      CurrentPage = <MainMenu />
      break;
  }

  return (
    <div className={clsx(scss.content)}>
      {CurrentPage}
    </div>
  )
}
function Browser({darkMode, setDarkMode}) {
  const theme = useTheme();
  const matchesSmNUP = useMediaQuery(theme.breakpoints.up('sm'));

  return (
    <>
    {matchesSmNUP && <div className={scss.placeholder}></div>}
    <div className={scss.Browser}>
      <TabRow darkMode={darkMode} />
      <AddressRow darkMode={darkMode} setDarkMode={setDarkMode} />
      <Content darkMode />
    </div>
    </>
  );
}

export default Browser;
